import "../App.css";
import Calendar from "./Calendar";
import Joyride from "react-joyride";

import { Step } from "react-joyride";

const steps: Step[] = [
  {
    target: "#setting",
    content: "",
    locale: { skip: "", next: "", back: "", last: "" },
    disableBeacon: true,
    spotlightClicks: true,
  },
];

export default function Home() {
  let run = false;
  if (localStorage.getItem("hourlyRate") === null) {
    run = true;
  }
  return (
    <>
      <Joyride
        continuous
        hideCloseButton
        run={run} // true
        scrollToFirstStep
        showProgress // 1/2
        showSkipButton
        steps={steps}
        spotlightClicks
        styles={{
          options: {
            zIndex: 10000,
          },
        }}
      />
      <Calendar />
    </>
  );
}
